/*
* PostPagination
*/
.pagination{
  padding: 0;
  margin-top: 12px;
  margin-bottom: 24px;
}

.pagination > li{
  display:  inline-block;
  font-size: 14px;
}

.pagination > li:last-child{
  float: right;
}

